@import url(https://fonts.googleapis.com/css2?family=Prompt:wght@100;200;600&family=Roboto+Mono:wght@100;400;700&display=swap);
*{margin:0;padding:0;box-sizing:border-box}.body{display:flex;flex-direction:column;color:#482728}.header{display:flex;flex-direction:column;align-items:center;align-self:center;justify-content:center;height:100vh;width:100vw;font-family:"Roboto Mono";font-weight:700}.page-sections{display:flex;flex-direction:column;justify-content:space-around;padding:50px;width:64vw;height:100vh}.section-flex{display:flex}.nav-link{text-decoration:none;color:#482728;font-family:"Roboto Mono";font-size:20px}.text-header{font-family:"Roboto Mono";font-weight:700;font-size:13vh}.text-sub-header{font-size:6vh;width:auto;font-family:"Prompt";font-weight:600}.descriptions{font-size:20px;width:40vw;font-family:"Prompt";font-weight:200}.roboto-mono-font-bold{font-family:"Roboto Mono";font-weight:700}.prompt-font-bold{font-family:"Prompt";font-weight:600}.prompt-font-thin{font-family:"Prompt";font-weight:200}ul{list-style-type:none}p{font-family:"Prompt";font-weight:200}hr{border-color:#482728}

.color-grid{display:flex;flex-wrap:wrap;width:36vw;height:100vh}.color-grid div{height:25vh}.color-grid div div{width:12vw}.orange{background-color:#FFAF47;color:#FFAF47}.beige{background-color:#FFEDD6;color:#FFEDD6}.red{background-color:#559CAD;color:#559CAD}.brown{background-color:#F2545B;color:#F2545B}

#footer{position:relative;display:flex}.footer-body{display:flex;flex-direction:column;align-items:center;justify-content:space-around;height:13vh;width:100vw;background-color:#FFEDD6}.footer-contact-info{display:flex;align-items:center;justify-content:space-between;width:90vw;font-family:"Roboto Mono";font-size:2.5vh}.footer-contact-info p{font-size:4vh}.footer-contact-info a{font-size:4vh;color:#482728}#footnote{font-family:"Roboto Mono";font-weight:100;font-size:2vh;color:#482728}

.home-about{background-color:#FFAF47}.home-skills{align-items:flex-end;background-color:#559CAD}.skills-descriptions div{display:flex}.skills-descriptions div h3{width:33.3%;padding:5px}.skills-descriptions div p{width:33.3%;padding:10px}.home-work{background-color:#F2545B}.learn-more-btn{align-self:flex-end;display:flex;font-family:"Crimson-text"}.project-section{display:flex;flex-direction:column;justify-content:space-around;align-items:center;width:36vw;background-color:#FFEDD6}.home-project-links{display:flex;align-items:center;font-family:"Roboto Mono";width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;font-weight:700;font-size:5vh;color:#482728}

#nav-bar{font-family:"Roboto Mono";display:flex;justify-content:space-between;position:absolute;align-items:center;background-color:transparent;width:80vw;height:8vh;margin:5vh 10vw;border-bottom:1px solid #482728}#nav-bar-ul{display:flex;justify-content:space-between}#nav-bar-ul li{padding-right:20px}.nav-bar-logo{color:#482728;padding-top:6vh;padding-left:2vw;font-size:11vh}.nav-bar-logo:link{color:#482728}

.project-descriptions div{display:flex}.project-descriptions div h3{width:40%;padding:5px}.project-descriptions div p{width:60%;padding:10px}.health-hub{background-color:#559CAD}.far-out-traveller{display:flex;background-color:#F2545B}.far-out-traveller div div{display:flex}.work-project-links{display:flex;align-items:center;font-family:"Roboto Mono";width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;padding-top:2vh;font-weight:700;font-size:5vh;color:#482728}

.contact-header{font-size:15vh}.contact-details{background-color:#FFEDD6}.contact-flex{display:flex;justify-content:space-around;flex-wrap:wrap;height:70vh}.contact-flex h2{font-family:"Roboto Mono";font-weight:400;font-size:5vh}.contact-flex div{display:flex;flex-direction:column;justify-content:space-between;align-items:center;height:15vh;width:20vw}.contact-flex p{font-size:3vh}.contact-flex a{color:#482728;font-size:8vh;padding-top:5vh}

.cv-education{background-color:#FFEDD6}#education-list{display:flex;justify-content:space-around}.cv-work-exp{background-color:#559CAD}.cv-volunteer{display:flex;background-color:#FFAF47}.cv-interests{background-color:#F2545B}.nav-link{align-self:flex-end}.interests-descriptions{display:flex}.interests-descriptions div{display:flex;flex-direction:column;align-items:center;width:33.3%;padding:5px}

